:root {
  --bg-color: #04480d;
  --text-color: #daf7a6;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: "Ubuntu", sans-serif;
}

#title {
  display: flex;
  margin: 50px auto 0 auto;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 200px;
  font-size: 200%;
}

#game,
#binary-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 70px;
}

button {
  font-size: 20px;
  font-family: "Ubuntu", sans-serif;
  font-weight: bold;
  background-color: var(--text-color);
  color: var(--bg-color);
  border: none;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  cursor: pointer;
}

p {
  margin: 5px;
}

.bold {
  font-weight: bold;
}

@media (orientation: portrait) {
  button {
    width: 40px;
    height: 40px;
    font-size: 15px;
  }

  #title {
    font-size: 20px;
    height: 100px;
    margin-bottom: 20px;
    margin-top: 20px;
  }
}
