#computed-number {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 70px;
}

.minusPlus {
  margin: 10px;
}

@media (orientation: portrait) {
  #computed-number {
    font-size: 50px;
  }

  #minus-plus-buttons {
    margin-top: -20px;
  }

  .minusPlus {
    margin: 2px;
  }
}
